
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import ValidateInput, { RulesProp } from '../components/ValidateInput.vue'
import ValidateForm from '../components/ValidateForm.vue'
import createMessage from '../components/createMessage'

export default defineComponent({
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Login',
  components: {
    ValidateInput,
    ValidateForm
  },
  setup () {
    const emailVal = ref()
    const router = useRouter()
    const store = useStore()
    const emailRules: RulesProp = [
      { type: 'required', message: '电子邮箱地址不能为空' },
      { type: 'email', message: '请输入正确的电子邮箱格式' }
    ]
    const passwordVal = ref('')
    const passwordRules: RulesProp = [
      { type: 'required', message: '密码不能为空' }
    ]
    // 登录函数 当结果正确时跳转
    const onFormSubmit = (result: boolean) => {
      if (result) {
        const payload = {
          email: emailVal.value,
          password: passwordVal.value
        }

        store.dispatch('loginAndFetch', payload).then(data => {
          if (data.status === 200) {
            createMessage('登录成功 2秒后跳转首页', 'success', 1000)
            store.commit('setLogin', true)
            store.dispatch('fetchColumn').then(rawdata => {
              const { data } = rawdata.data
              const currentColumn = data
              console.log(currentColumn)
              if (currentColumn) {
                store.commit('setUserCid', currentColumn.id)
              }
              if (currentColumn === null) {
                router.push('/createColumn')
              } else {
                const payload = {
                  current: '1',
                  size: '5'
                }
                // const cid = currentColumn.id
                store.dispatch('fetchmyPosts', payload)
                router.push('/')
              }
            })
          } else {
            router.push('/login')
          }
        }).catch(e => {
          // 捕捉错误
          console.log(e)
        })
      }
    }
    return {
      emailRules,
      emailVal,
      passwordVal,
      passwordRules,
      onFormSubmit
    }
  }
})
